import request from '../utils/request'

// {articleId: ?}
export function getCommentByArticleId (params) {
  return request({
    methods: 'GET',
    url: '/ai-getComment',
    params
  })
}

// {parentId: ?,name: ?,email: ?,desc: ?,articleId: ?}
export function saveComment (params) {
  return request({
    methods: 'POST',
    url: '/ai-saveComment',
    params
  })
}



